<template>
    <div class="history flex-container bg-grey">
        <div class="header shadow">
            <div class="container padding top">
                <div class="logo-wrap">
                    <div class="logo">
                        <img src="@/assets/logo.png" alt="aten logo">
                    </div>
                    <div class="header-title">
                        <span class="title">历史版本</span>
                    </div>
                </div>
                <div class="search" :class="{ 'is-active': focus }">
                    <input type="text" @focus="focus = true" @blur="focus = false" v-model="keyword" placeholder="输入关键词搜索...">
                </div>
                <div class="intro">
                    <router-link to="/index/" v-if="userlimit > 100">管理资源</router-link>
                    <a href="javascript:" @click="logout" v-else>退出登录</a>
                </div>
            </div>
        </div>
        <div class="container scroller thin-container">
            <div class="versions">
                <div class="item version" v-for="(soft, index) in list" :key="index">
                    <div :class="'title ' + mapFormat(soft.file)">
                        <h3>{{ soft.file || '无文件名' }}</h3>
                    </div>
                    <div class="flex-space-between sub-font">
                        <p>版本: {{ soft.version }}</p>
                        <p>发布日期：{{ soft.created_at | formatDate }}</p>
                    </div>
                    <div class="flex-space-between sub-font">
                        <p>hash: {{ soft.hash }}</p>
                        <p class="el-icon-download">下载次数: {{ soft.download }}</p>
                    </div>
                    <div class="changelog">
                        <div class="title">更新日志</div>
                        <div class="log">
                            <p>{{ soft.log || '暂无' }}</p>
                        </div>
                    </div>
                    <div class="operation">
                        <a class="link el-icon-download" :href="filePath(soft)" :download="soft.file">下载</a>
                    </div>
                </div>
                <div class="empty-list" v-if="!list.length">
                    <p>暂无符合条件的内容</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { listHistory, getBaseUrl } from '@/libs/request';
import { mapFormat } from '@/libs/tool';
import { formatDate } from '@/libs/moment';
import { getToken } from '@/libs/auth';
import { mapState } from 'vuex';

export default {
    props: ['id'],
    data: () => ({
        focus: false,
        history: [],
        list: [],
        keyword: ''
    }),
    methods: {
        listHistory(callback) {
            listHistory({ resource_id: this.id }, {
                success: res => {
                    this.history = res.resources;
                    callback();
                },
                fail: err => {
                    this.$message.error(err)
                }
            })
        },
        mapFormat,
        filePath({ resource_id, version }) {
            return getBaseUrl() + 'resource/download?id=' + resource_id + '&version=' + version + '&token=' + getToken();
        },
        search() {
            let { keyword } = this;
            this.list = keyword === '' ? this.history : this.history.filter(item => {
                return item.file.indexOf(keyword.toLowerCase()) > -1
            })
        },
        logout() {
            removeCookie();
            this.$router.push('/');
        }
    },
    computed: {
        ...mapState(['userlimit'])
    },
    mounted() {
        this.listHistory(this.search)
    },
    filters: { formatDate },
    watch: {
        keyword: 'search'
    },
}
</script>

<style scoped>
.versions {
    margin: 25px;
    background: #fff;
    border-radius: 5px;
}

.versions .version {
    padding: 20px;
    border-top: 1px solid #eee;
}

.versions .version:first-child {
    border-top-color: transparent;
}

.sub-font {
    font-size: 14px;
    color: #777;
}

.sub-font p {
    line-height: 25px;
}
</style>
